import {playMode} from 'common/js/config'
import {loadSearch, loadPlayHistory, loadFavorite} from 'common/js/cache'

const state = {
  themeImg: require('common/image/bagua.png'),
  headerTitle: 'iMusic',
  headerBack: {},
  singer: {},
  playing: false,
  fullScreen: false,
  playList: [],
  sequenceList: [],
  mode: playMode.sequence,
  currentIndex: -1,
  disc: {},
  toplist: {},
  searchHistory: loadSearch().slice(),
  playHistory: loadPlayHistory().slice(),
  favorite: loadFavorite().slice(),
  vkey: ''
}
export default state


